* {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}


:root {
    --surface-color: white;
    --curve: 20;
  }
  
  * {
    box-sizing: border-box;
  }
  


  .minicard {
    position: relative;
    display: block;
    height: 100%;  
    border-radius: calc(20 * 1px);  
    border-radius: calc(var(--curve) * 1px);
    overflow: hidden;
  }
  
  .minicard-image {      
    width: 100%;
    height: 100%;
  }
  
  .minicard-overlay {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;      
    border-radius: calc(20 * 1px);      
    border-radius: calc(var(--curve) * 1px);    
    background-color: white;    
    background-color: var(--surface-color);      
    -webkit-transform: translateY(100%);      
            transform: translateY(100%);
    transition: .2s ease-in-out;
  }
  
  .minicard:hover .minicard-overlay {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  
  .minicard-header {
    position: relative;
    display: flex;
    align-items: center;
    grid-gap: 2em;
    gap: 2em;
    padding: 1em;
    border-radius: calc(20 * 1px) 0 0 0;
    border-radius: calc(var(--curve) * 1px) 0 0 0;    
    background-color: white;    
    background-color: var(--surface-color);
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    transition: .2s ease-in-out;
  }
  
  .minicard-arc {
    width: 80px;
    height: 80px;
    position: absolute;
    bottom: 100%;
    right: 0;      
    z-index: 1;
  }
  
  .minicard-arc path {
    fill: white;
    fill: var(--surface-color);
    d: path("M 40 80 c 22 0 40 -22 40 -40 v 40 Z");
  }       
  
  .minicard:hover .minicard-header {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    
  }
  
  .minicard-thumb {
    flex-shrink: 0;
    width: 50px;
    height: 50px;      
    border-radius: 50%;      
  }
  
  .minicard-title {
    font-size: 1em;
    margin: 0 0 .3em;
    color: black;
  }
  
  .minicard-status {
    font-size: .8em;
    color: black;
  }
  
  .minicard-description {
    padding: 0 1.5em 1.5em;
    margin: 0;
    color: black;
    font-family: "MockFlowFont";   
    overflow: hidden;
  }    




  
:root {
    --search-button-color: black;
  }

.search-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vh;
    width: 30vw;
}

.search-container{
    margin-bottom: 10px;
    position: relative;
}

.search-input{
    box-sizing: border-box;
    height: 50px;
    width: 50px;
    border-style: none;
    padding: 10px;
    font-size: 18px;
    letter-spacing: 2px;
    outline: none;
    border-radius: 222px;
    transition: all .5s ease-in-out;
    background: black;
    background: var(--search-button-color);
    padding-right: 40px;
    color:black;
}

.search-input::-webkit-input-placeholder{
    box-sizing: border-box;
    color: black;
    font-size: 18px;
    letter-spacing: 2px;
    font-weight: 100;
}

.search-input:-ms-input-placeholder{
    box-sizing: border-box;
    color: black;
    font-size: 18px;
    letter-spacing: 2px;
    font-weight: 100;
}

.search-input::placeholder{
    box-sizing: border-box;
    color: black;
    font-size: 18px;
    letter-spacing: 2px;
    font-weight: 100;
}

.search-input:focus{
    width: 300px;
    border-radius: 0px;
    background-color: transparent;
    border-bottom:1px solid black;
    transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}

.search-button{
    box-sizing: border-box;
    width: 50px;
    height: 50px;
    border-style: none;
    font-size: 20px;
    font-weight: bold;

    cursor: pointer;
    border-radius: 50%;
    position: absolute;
    right: 0px;
    color:white ;
    background-color:transparent;
}

.search-button:focus ~ .search-input{
    width: 300px;
    border-radius: 0px;
    background-color: transparent;
    border-bottom:1px solid black;
    transition: all 500ms cubic-bezier(0, 0.110, 0.35, 2);
}
  
  
 

