* {
  box-sizing: border-box;
}

.timeline {
  position: relative;
  margin: 20px;
}
.timeline:before {
  
  background-color: black;
  content: '';
  margin-left: -1px;
  position: absolute;
  top: 0;
  left: 2em;
  width: 2px;
  height: 100%;
}

.timeline-event {
  width: 325px;
  position: relative;
}
.timeline-event:hover .timeline-event-icon {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  background-image: linear-gradient(90deg, #FFEF7E, #B7F9E9);
}
.timeline-event:hover .timeline-event-date {
  box-shadow: inset 12em 0 0 0 #B7F9E9;
  color: black;
}

.timeline-event-info {
  padding: 2em;
  position: relative;
  top: -1.875em;
  left: 3em;
  width: 10%;
  font-size: 1.25em;
}

.timeline-event-icon {
  transition: -webkit-transform 0.2s ease-in;
  transition: transform 0.2s ease-in;
  transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background-color: white;
  outline: 10px solid black;
  display: block;
  margin: 0.5em 0.5em 0.5em -0.5em;
  position: absolute;
  top: 0;
  left: 2em;
  width: 1em;
  height: 1em;
  
}

.timeline-event-date {
  background-color: black;
  color: white;
  display: inline-block;
  
  transition: color 0.5s, box-shadow 0.5s ease-in 0.1s;
  font-size: 0.75em;
  box-shadow: inset 0 0 0 0em grey;
  
  margin-bottom: 1.2em;
  
  padding: 0.25em 3em 0.2em 3em;
  cursor: pointer;
}


.timeline-event h4 {
  color: black;
  font-weight: 100;
  text-align: left;
  font-size: 1.55em;
  cursor: pointer;
}

.timeline-event h6 {
  color: black;
  font-weight: 100;
  text-align: left;
  font-size: 0.75em;
}


:root {
    --font-family-primary: "Vollkorn", serif;
    --font-family-secondary: "PT Sans", sans-serif;
    --font-size-title: 32px;
    --line-height-title: 1.4;
    --font-size-caption: 14px;
    --line-height-caption: 1.2;
    --color-text: #222022;
    --color-highlight-primary: #FFEF7E;
    --color-highlight-secondary: #B7F9E9;
    --border-radius-primary: 32px;
  }


  .card {
    border-radius: 32px;
    border-radius: var(--border-radius-primary);
    box-shadow: 24px 24px 80px rgba(0, 0, 0, 0.1);
    padding: 20px 20px 28px 20px;
    box-sizing: border-box;
    margin: 20px;
    display: flex;
    flex-direction: column;
  }
  
  
  .card__image {
    object-fit: cover ;
    width: 100%;
    height: 100%;
    border-radius: 32px;
    border-radius: var(--border-radius-primary);
    margin-bottom: 18px;
  }
  
  .card__date {
    display: block;
    font-family: "PT Sans", sans-serif;
    font-family: var(--font-family-secondary);
    font-size: 14px;
    font-size: var(--font-size-caption);
    line-height: 1.2;
    line-height: var(--line-height-caption);
    text-transform: uppercase;
    color: #222022;
    color: var(--color-text);
    margin-bottom: 6px;
  }
  
  .card__title {
    font-family: "Vollkorn", serif;
    font-family: var(--font-family-primary);
    font-size: 32px;
    font-size: var(--font-size-title);
    line-height: 1.4;
    line-height: var(--line-height-title);
    color: #222022;
    color: var(--color-text);
    -webkit-box-decoration-break: clone;
            box-decoration-break: clone;
    background-image: linear-gradient(90deg, #FFEF7E, #B7F9E9);
    background-image: linear-gradient(90deg, var(--color-highlight-primary), var(--color-highlight-secondary));
    background-size: 100% 42%;
    background-repeat: no-repeat;
    background-position: 0 85%;
    padding: 0 4px;
    margin-left: -4px;
  }

  .card__title2 {
    font-family: "Vollkorn", serif;
    font-family: var(--font-family-primary);
    font-size: 25px;
    line-height: 1.4;
    line-height: var(--line-height-title);
    color: #222022;
    color: var(--color-text);
    -webkit-box-decoration-break: clone;
            box-decoration-break: clone;
    background-image: linear-gradient(90deg, #FFEF7E, #B7F9E9);
    background-image: linear-gradient(90deg, var(--color-highlight-primary), var(--color-highlight-secondary));
    background-size: 100% 20%;
    background-repeat: no-repeat;
    background-position: 0 85%;
    padding: 0 4px;
    margin-left: -4px;
  }

  .card__date2 {
    display: block;
    font-family: "PT Sans", sans-serif;
    font-family: var(--font-family-secondary);
    font-size: 10px;
    line-height: 1.2;
    line-height: var(--line-height-caption);
    text-transform: uppercase;
    color: #222022;
    color: var(--color-text);
    margin-bottom: 6px;
  }


  



