.phone {
  margin: 41px;
  width: 21rem;
  height: 45rem;
  box-shadow: 0 4px 35px rgba(0,0,0,.1);
  border-radius: 40px;
  display: flex;
  flex-direction: column;
}

.circle, .circle-loading {
  position: relative;
  border-radius: 100%;
  width: 8rem;
  height: 8rem;
  background: linear-gradient(40deg, #FFEF7E, #B7F9E9 70%);
  background: linear-gradient(40deg, var(--color-highlight-primary), var(--color-highlight-secondary) 70%);
  margin: auto;
}

.circle-loading {
  -webkit-animation: corner 1s;
          animation: corner 1s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

.circle-status {
  position: relative;
  width: 8rem;
  height: 8rem;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 32px;
}

p { 
  font-size: 90%;
}

.panel-heading {
  font-size: 140%;
  font-weight: bolder;
  margin: 3rem 0 .2rem 0;
}

.panel-info {

  margin: 2rem 0 .2rem 0;
}

.panel-input {

  margin: 6rem 0 .2rem 0;
}




.mark {
  border-radius: 100px;
  background-color: black;
}
.mark {
  width: 7%;
  margin: auto;
  position: relative;
  height: .25rem;
}

.mark::before {
  content: '';
  position: absolute;
  width: .25rem;
  height: 100%;
  left: -70%;
  opacity: .45;
  background-color: inherit;
}
.mark::after {
  content: '';
  position: absolute;
  width: .25rem;
  height: 100%;
  right: -70%;
  opacity: .45;
  background-color: inherit;
}

.mark-both {

  margin-top: 60px;
  justify-content: space-between;
  display: flex;
  align-items: center;
}

.mark-on {
  -webkit-text-stroke: 0.2px black;

  opacity: 1;
  color: greenyellow;
}

.mark-off {
  opacity: .35;
  font-size: 15px;
}


/* BOTTOM */


.panel-bottom-1, .panel-bottom-2 {
  padding: 1.5rem 1.8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.panel-bottom-2 {
  justify-content: space-between;
}

.panel-bottom-1 p, .panel-bottom-2 p {
  opacity: .55;
  font-size: 90%;
  cursor: pointer;
}
.panel-bottom-1 p:hover, .panel-bottom-2 p:hover {
   text-decoration: underline;
}

.panel-button, .panel-button-disabled{
  box-shadow: 0 2px 10px rgba(0,0,0,.2);
  border-radius: 100%;
  width: 3rem;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer;
  transition: -webkit-transform .5s cubic-bezier(0.19, 1, 0.22, 1);
  transition: transform .5s cubic-bezier(0.19, 1, 0.22, 1);
  transition: transform .5s cubic-bezier(0.19, 1, 0.22, 1), -webkit-transform .5s cubic-bezier(0.19, 1, 0.22, 1);
}

.panel-button-disabled {
  cursor: not-allowed;
  opacity: .3;
}

.panel-button:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}


@-webkit-keyframes corner {
  0% {
    border-top-left-radius: 1% 50%;
  }
  12.5% {
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
  }
  25% {
    border-top-right-radius: 1% 50%;
  }
  25.001% {
    border-top-right-radius: 50% 1%;
  }
  37.5% {
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
  }
  50% {
    border-bottom-right-radius: 50% 1%;
  }
  50.001% {
    border-bottom-right-radius: 1% 50%;
  }
  62.5% {
    border-bottom-right-radius: 50%;
    border-bottom-left-radius: 50%;
  }
  75% {
    border-bottom-left-radius: 1% 50%;
  }
  75.001% {
    border-bottom-left-radius: 50% 1%;
  }
  87.5% {
    border-bottom-left-radius: 50%;
    border-top-left-radius: 50%;
  }
  100% {
    border-top-left-radius: 50% 1%;
  }
}


@keyframes corner {
  0% {
    border-top-left-radius: 1% 50%;
  }
  12.5% {
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
  }
  25% {
    border-top-right-radius: 1% 50%;
  }
  25.001% {
    border-top-right-radius: 50% 1%;
  }
  37.5% {
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
  }
  50% {
    border-bottom-right-radius: 50% 1%;
  }
  50.001% {
    border-bottom-right-radius: 1% 50%;
  }
  62.5% {
    border-bottom-right-radius: 50%;
    border-bottom-left-radius: 50%;
  }
  75% {
    border-bottom-left-radius: 1% 50%;
  }
  75.001% {
    border-bottom-left-radius: 50% 1%;
  }
  87.5% {
    border-bottom-left-radius: 50%;
    border-top-left-radius: 50%;
  }
  100% {
    border-top-left-radius: 50% 1%;
  }
}








